import uniRate from "@/components/uni-rate/uni-rate.vue"
import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";


export default {
	components: {
		uniRate,
		uniLoadMore
	},

	data() {
		return {
			value: 2,
			productList: [],
			totalList: [],
			status: "more",
			current_page: 1,
			total: "",
			last_page: "1",
			loading: "more"
		}
	},
	methods: {
		//跳转到webview页面
		skipWeb(url) {
			// var linkurl = JSON.stringify(url)
			// console.log(linkurl)
			// uni.setStorage({
			// 	key: "url",
			// 	data: url,
			// 	success: () => {
			// 		uni.navigateTo({
			// 			url: "../webview/webview"
			// 		})
			// 	}
			// })
			wx.navigateToMiniProgram({
				appId: 'wx6885acbedba59c14', // 要跳转的小程序的appid
				path:  url, // 跳转的目标页面
				extarData: {
					
				},
				success(res) {
					console.log(res)
				}
			})
		},
		//电话
		callPhone(phone) {
			uni.makePhoneCall({
				phoneNumber: phone
			});
		},
		getgoods() {
			this.loading = "loading";

			this.request({
				url: this.config.url + "express",
				method: "post",
				data: {
					page: this.current_page
				},
				success: res => {
					console.log(res)
					var len;
					if (res.data.code == 1) {
						// 商品列表
						this.totalList = res.data.data.data;
						var page = res.data.data.current_page;
						this.last_page = res.data.data.last_page;
						this.total = res.data.data.data.total;
						this.status = "more";
						//每页10 
						if (page == 1) {
							if (this.totalList.length < 10) {
								len = this.totalList.length;
								this.loading = "noMore";
							} else {
								len = 10;
							}
							for (var i = 0; i < len; i++) {
								this.productList.push(this.totalList[i])
								//	console.log(this.productList)
							}
						} else {
							this.productList.concat(res.data.data.data)
						}





					}
				}
			})
		}
	},
	mounted() {
		this.getgoods()
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom() {
		// 调用获取推荐列表接口
		// 当前页小于最后一页才调用
		this.current_page++
		if (this.current_page < this.last_page) {
			this.getgoods();
		} else if (this.current_page == this.last_page) {
			this.getgoods();
			this.loading = "noMore";
		} else {
			this.loading = "noMore";
		}

	}
}
